
















import {Component, Emit, Mixins, Prop, Watch} from "vue-property-decorator";
import AsModal from "../../mixins/AsModal";

@Component
export default class EditAdminAccountModal extends Mixins(AsModal) {
  @Emit("refresh") refresh(): void {}
  @Prop(Number) readonly id!: number;
  @Prop(String) readonly name!: string;

  editName: string = "";
  editId: number = 0;

  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val) {
      this.editName = this.name;
      this.editId = this.id;
    } else {
      this.editName = "";
      this.editId = 0;
    }
  }

  submit(): void {
    this.api.patchAccountName(this.editName, this.editId).then(() => {
      this.refresh();
      this.clean();
    });
  }

  clean(): void {
    this.dismiss();
  }
}
