
































import {paramsToQuery} from "@/core/utils/paramsToQuery";
import {LabSerializerWithPopulate} from "@/types/ApiTypes";
import {TABS_ACTION, TabsActions} from "@/types/store/TabStore";
import {Component, Vue} from "vue-property-decorator";
import {Action, Getter} from "vuex-class";

@Component
export default class MyLabsView extends Vue {
  @Getter("permission/status") readonly status!: number;
  @Getter("permission/id") readonly id!: number;
  @Action(TABS_ACTION.UPDATE_CURRENT_TAB)
  updateTab!: TabsActions["UPDATE_CURRENT_TAB"];

  goToTab(tabName: string): void {
    this.$router.push({query: {tab: tabName}});
    this.updateTab(tabName);
  }

  goToProject(lab: LabSerializerWithPopulate): void {
    this.$router.push({query: {tab: "ProjectManager", id: String(lab.id)}});
    this.updateTab("ProjectManager");
  }
  data: LabSerializerWithPopulate[] = [];

  mounted(): void {
    this.getData();
  }

  getData(): void {
    let query = null;
    if (this.status === 0) {
      query = paramsToQuery({user: this.id});
    }
    this.api.getLabList({query}).then((res) => {
      this.data = res;
    });
  }

  labName(lab: LabSerializerWithPopulate): string {
    const deviceOnline = lab.device_manager.online_device ? "device online" : "device offline";
    return this.status === 0
      ? lab.name + ` ( ${deviceOnline} )`
      : lab.name + ` ( ${lab.user.account} & ${deviceOnline} )`;
  }
}
